import Joi from '@hapi/joi';

import { ArchiveSource, ArchiveSourceSchema } from '../archiveSource';
import { Platform } from '../constants';

const iOSCredentialsSchema = Joi.object().keys({
  provisioningProfileBase64: Joi.string().required(),
  distributionCertificate: Joi.object({
    dataBase64: Joi.string().required(),
    password: Joi.string().required(),
  }).required(),
});

interface iOSCredentials {
  provisioningProfileBase64: string;
  distributionCertificate: DistributionCertificate;
  secretEnvs?: Record<string, string>;
}

interface DistributionCertificate {
  dataBase64: string;
  password: string;
}

const BaseiOSJobSchema = Joi.object().keys({
  projectArchive: ArchiveSourceSchema.required(),
  platform: Joi.string().valid(Platform.iOS).required(),
  projectRootDirectory: Joi.string().required(),
  releaseChannel: Joi.string(),
  secrets: Joi.object({
    buildCredentials: iOSCredentialsSchema,
    secretEnvs: Joi.object().pattern(Joi.string(), Joi.string()),
  }).required(),
});

interface BaseiOSJob {
  projectArchive: ArchiveSource;
  platform: Platform.iOS;
  projectRootDirectory: string;
  releaseChannel?: string;
  secrets: {
    buildCredentials?: iOSCredentials;
    secretEnvs?: Record<string, string>;
  };
}

export { BaseiOSJob, BaseiOSJobSchema, iOSCredentials, iOSCredentialsSchema, DistributionCertificate };
